import { Inter } from "next/font/google";
import Image from "next/image";

const Interfont = Inter({
  subsets: ["latin"],
  weight: "400",
});

export default function Box() {
  return (
    <div
      className={`${Interfont.className} flex flex-wrap md:flex-nowrap items-center justify-between bg-amber-100 p-6 md:p-10 min-h-[28rem]`}
    >
      {/* Teks */}
      <span className="text-3xl md:text-5xl font-semibold text-gray-900 md:w-1/2 w-full mb-6 md:mb-0">
        <span>YINLINOKU</span>
        <span className="text-red-600"> Decentralized </span>
        <br /> Donation for people
        <br />
        <span className="text-xl md:text-2xl">
          donasikan hartamu menggunakan teknologi{" "}
          <span className="text-amber-900">Blockchain</span>
        </span>
      </span>

      {/* Gambar */}
      <div className="w-full md:w-auto flex justify-center md:justify-end">
        <Image
          src="/character1.png"
          alt="Character"
          width={300}
          height={200}
          className="max-w-full h-auto"
        />
      </div>
    </div>
  );
}
